span.commit a {
    width: 55px;
    overflow: hidden;
    -o-text-overflow: "";
       text-overflow: "";
    display: inline-block;
    margin-bottom: -6px;
}
.ui.grid.run {
    margin-bottom: 20px;
}
.ui.header {
    opacity: 0.7;
}
.DataList {
    height: 200px;
    overflow-y: auto;
}

.DataListSearchBox {
    padding-bottom: 6px;
}

.DataListWithSearch {
    height: 170px;
    overflow-y: auto;
    width: 100%;
}

.vars .ui.list .item {
    padding: 6px;
}
.vars .ui.list .header {
    float: left;
    opacity: 0.7;
}
.vars .ui.list .description {
    float: right;
    max-width: 200px;
    overflow: hidden;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

i.icon.natural-log {
    font-family: fontello;
}
i.icon.natural-log:before {
    content: "\e800";
}

i.icon.wandb {
    font-family: fontello;
}
i.icon.wandb:before {
    content: "\e801";
}

.rv-discrete-color-legend {
    font-size: 11px;
    min-height: 40px;
    max-height: 60px;
    overflow: scroll;
    overflow-x: hidden;
    overflow-y: hidden;
}

@font-face {
    font-family: "fontello";
    src: url("data:application/octet-stream;base64,")
            format("woff"),
        url("data:application/octet-stream;base64,")
            format("truetype");
}



